            <div>11、隐私政策</div>
            <p>11.1信息可能收集的范围与方式</p>
            <div class="child">
                <p>11.1.1计算未来公司提供服务时，可能会收集、储存和使用下列与您有关的信息。如果您不提供相关信息，可能无法注册成为采购精灵的用户或无法享受计算未来公司提供的某些服务，或者无法达到相关服务拟达到的效果。</p>
                <p>11.1.2 用户理解并同意当您使用“采购精灵”软件及相关服务时填写和/或提供的信息，包括用户真实姓名、性别、出生年月日、身份证号、手机号码、银行账户、邮箱、地址等单独或者结合地识别用户身份信息。在您使用“采购精灵”软件及相关服务时，计算未来公司自动接收并记录用户前述个人信息以及操作状态以及使用习惯等一些明确且客观反映在计算未来公司服务器端的基本记录信息。</p>
            </div>
            <p>11.2因您使用“采购精灵”软件及相关服务获取的信息</p>
            <div class="child">
                <p>11.2.1日志信息，当您使用“采购精灵”软件及相关服务时，服务器会自动记录一些信息，例如cookies、web beacon或其他方式自动采集的技术信息。</p>
                <p>基于为您提供更个性化的用户体验和服务，当您与我们提供给合作伙伴的服务（例如广告和/或推广服务，以及可能显示在其他网站上的由我们提供的服务功能）进行交互时，我们允许cookie或者匿名web beacon发送给我们的服务器，向您提供与您相关的广告而非进行普遍的广告投放。</p>
                <p>11.2.2设备或应用信息，某些移动设备或应用包含唯一应用程序编号。例如您使用的移动设备、浏览器或您使用的用于接入“采购精灵”服务的其他程序所提供的配置信息、设备版本号和设备识别码、IP地址等。</p>
                <p>为了提供更好的服务与改善用户体验，我们可能会记录硬件型号、操作系统版本号、国际移动设备身份码（IMEI）、网络设备硬件地址（MAC）等信息。</p>
                <p>11.2.3位置信息，当您开启设备定位功能并使用“采购精灵”基于位置提供的相关服务时，系统会通过GPS或WiFi等方式自动处理有关设备的位置信息，以使得您不需要手动输入自身地理坐标就可获得相关服务。您可以通过关闭定位功能，停止对您的地理位置信息的收集。</p>
            </div>
            <p>11.3信息使用</p>
            <div class="child">
                <p>11.3.1通过使用收集的信息，计算未来公司会得以向您提供个性化的服务并改善现有服务。</p>
                <p>11.3.2通过使用收集的信息，向您提供更加相关广告以替代普遍投放的广告。</p>
                <p>11.3.3计算未来公司可能会与计算未来关联公司、合作伙伴及第三方服务供应方等共同向您提供您所要求的服务或者共同向您提供服务或展示您可能感兴趣的内容。在服务或内容为该项产品/服务所必须的情况下，您同意计算未来公司可与其分享必要的信息，并且计算未来公司将努力确保该第三方在使用您的个人信息时同时遵守本隐私政策以及其他的保密和安全义务。</p>
                <p>11.3.4计算未来公司不对外公开或向第三方提供用户的个人信息，但下列情况除外：</p>
