            <p>12.4 计算未来公司依据本协议约定获得处理违法违规内容的权利，该权利不构成计算未来公司的义务或承诺，计算未来公司不能保证及时发现违法行为或进行相应处理。</p>
            <p>12.5 用户明确了解并同意：关于本协议服务，计算未来公司不提供任何种类的明示或暗示担保或条件，包括但不限于商业适售性、特定用途适用性等。您对本协议软件及相关服务的使用行为必须自行承担相应风险。</p>
            <p>12.6 用户明确了解并同意，本协议是在保障遵守国家法律法规、维护公序良俗，保护他人合法权益，计算未来公司在能力范围内尽最大的努力按照相关法律法规进行判断，但并不保证计算未来公司判断完全与司法机关、行政机关的判断一致，如因此产生的后果用户已经理解并同意自行承担。</p>
