            <p>13.2 您可以在“采购精灵”软件中开启和使用上述单项服务功能。某些单项服务可能需要您同时接受就该服务特别制订的协议或者其他约束您与该项服务提供者之间的规则。计算未来公司将以醒目的方式提供这些协议、规则供您查阅。一旦您开始使用上述服务，则视为您同时接受有关单项服务的相关协议、规则的约束。</p>
            <p>13.3 如您可能在“采购精灵”软件中使用第三方提供的软件及相关服务时，除遵守本协议及本采购精灵相关规则外，还应遵守第三方的协议、相关规则。如因第三方软件及相关服务产生的争议、损失或损害，由用户与第三方解决。</p>
