            <p>15.3本协议可能因国家政策、履行环境发生变化或计算未来公司的需要而进行修改，计算未来公司会将修改后的协议予以发布。若您对修改后的协议有异议的，请立即停止登录、使用“采购精灵”软件及相关服务，若您登录或继续使用“采购精灵”软件及相关服务，视为认可修改后的协议。</p>
